.xop-section {
	max-width: 800px;
	margin: 0 auto;
    padding: 6% 2%;
    align-items: center;
    left: 20px;
}

.xop-grid {
	margin: 20px 0 0 0;
	padding: 0;
	list-style: none;
	display: block;
	text-align: center;
    width: 100%;
    left: 20px;
}

.xop-grid:after {
	clear: both;
}

.xop-grid:after, .xop-box:before {
	content: '';
	display: table;
}

.xop-grid li {
	width: 328px;
	height: 328px;
	display: inline-block;
	margin: 20px;
}

.xop-grid li {
	width: 328px;
	height: 328px;
	display: inline-block;
	margin: 20px;
}

.xop-box {
	width: 300px;
	height: 300px;
	position: relative;
	cursor: pointer;
    border-radius: 10px;
 transition: all 0.3s ease-in-out, 
    transform 0.3s ease-in-out;
    /* right: 10px; */
    border: 2px solid #FFD700;
    box-shadow: 0 0 10px #FFD700,0 0 0px #FFD700,0 0 120px #FFD700;
    /* box-shadow: 0 5px 25px 0 #2196f3 inset ,0 5px 25px 0 #2196f3,
                0 5px 25px 0 #2196f3 inset ,0 5px 25px 0 #2196f3; */
}

.xop-box:hover {
	transform: scale(1.05); 
}

